.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position:fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
    
}
.header h1{
    font-size:3rem;
}
.header-bg{
    background-color:rgba(43, 42, 42, 0.85);
    transition:0.5s;
}
.nav-menu{
    display:flex;
   
}

.nav-menu li{
    padding:0 1rem;
}
.nav-menu li a{
    font-size: 1.7rem;
    font-weight:500;  
    
}
.hamburger{
    display:none;
}
@media screen and (max-width: 1024px){
    
    .hamburger{
        display:initial;
    }
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width:100%;
        height: 100vh;
        background:rgba(0,0,0,0.9);
        position:absolute;
        top:0;
        left:-100%;
        z-index: -3;
        transition:0.3s;

    }
    .nav-menu.active{
        left:0;
    }
    
    .nav-menu li{
        padding:1rem;
    }
    .nav-menu li a{
        font-size:2rem;
    }
    
}
